import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import { styled } from '@mui/system';

const CustomHeaderTableCell = styled(TableCell)({
  backgroundColor: '#666666',
  color: 'white',
  textAlign: 'center'
});

const CustomTableCell = styled(TableCell)({
  backgroundColor: 'white',
});

function createData(name, calories, fat, carbs, protein) {
  return { name, calories, fat, carbs, protein };
}

const rows = [
  createData('요거트', 159, 6.0, 24, 4.0),
  createData('아이스크림샌드위치', 237, 9.0, 37, 4.3),
  createData('초코케익', 262, 16.0, 24, 6.0),
  createData('컵케익', 305, 3.7, 67, 4.3),
  createData('생강쿠키', 356, 16.0, 49, 3.9),
];



export default function BasicTable() {
  
  return (
    <TableContainer component={Paper} style={{width:'80%', margin:'auto'}} >
      <Table sx={{ minWidth: 500 }} aria-label="simple table" >
        <TableHead>
          <TableRow>
            <CustomHeaderTableCell >Dessert (100g serving)</CustomHeaderTableCell>
            <CustomHeaderTableCell  align="right">Calories(칼로리)</CustomHeaderTableCell>
            <CustomHeaderTableCell  align="right">Fat(지방)&nbsp;(g)</CustomHeaderTableCell>
            <CustomHeaderTableCell  align="right">Carbs(탄수화물)&nbsp;(g)</CustomHeaderTableCell>
            <CustomHeaderTableCell  align="right">Protein(단백질)&nbsp;(g)</CustomHeaderTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              // sx={{ '&:last-child td, &:last-child th': { border: 1 } }}
            >
              <CustomHeaderTableCell component="th" scope="row">
                {row.name}
              </CustomHeaderTableCell>
              <CustomTableCell align="right">{row.calories}</CustomTableCell>
              <CustomTableCell align="right">{row.fat}</CustomTableCell>
              <CustomTableCell align="right">{row.carbs}</CustomTableCell>
              <CustomTableCell align="right">{row.protein}</CustomTableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}