import React, { useState } from "react"; 
 
function Ex1(){ 
    
    //let count = 0;
    // 변수와 그변수를 수정할 수 있는 setter함수정의
    const [count ,setCount] = useState(0);

    function btnClick(){
        //alert('Clicked');
        //count++;
        //console.log(count);
        //setCount함수내에서 count수정->자체컴포넌트를 rendering(갱신)
        setCount(count+1);
        
    }
    
    return ( 
        <div>
             클릭횟수1 : { count }<br/>
             <input type="button" value="클릭1" onClick={ btnClick } /> &nbsp;
             <input type="button" value="클릭2" onClick={ function(){ setCount(count+2) } } />&nbsp;
             <input type="button" value="클릭3" onClick={ ()=> setCount(count+3) } />
             <br/>
             클릭횟수2 : { count }<br/>
        </div> 
    ); 
} 
export default Ex1;