/* eslint-disable */ 
import React, { useState } from "react"; 
 
function Ex2(){ 

    const [txt,setTxt] = useState("");

    function setChange(e){
       //console.log(e.target.value);
       // txt갑변경후 다시 화면을 갱신한다(rendering)
       setTxt(e.target.value);
    }

    return ( 
        <div>
           입력 메시지: <input  value={txt}  onChange={ setChange }  /> <br/>
           출력 메시지: { txt }

        </div> 
    ); 
} 
export default Ex2;