/* eslint-disable */ 
import axios from "axios";
import React, { useEffect, useState } from "react"; 

// import table from "../css/table.css"
import 'bootstrap/dist/css/bootstrap.min.css'

function Ex4(){ 

    axios.defaults.withCredentials = true;
  
    const [data,setData] = useState([]);
    const [visit,setVisit] = useState({name:"",content:"",pwd:""});

    // async function getData0(){
    //      const res = await axios.get('http://43.202.59.136:8080/demo_visit/rest/visits');
    //      //console.log(res.data.list);
    //      setData(res.data.list);
    // }


    function getData(){
         //axios.defaults.withCredentials = true;
         const url = 'https://3.35.18.174.nip.io/demo_visit/rest/visits';
         //const url = 'http://192.168.0.49:8080/rest/visits';
         axios.get(url)
              .then((res)=>{
                //console.log('total_count',res.data.total_count);
                setData(res.data.list);
              })
              .catch((error)=>{
                console.log(error);
              });
         //console.log(res.data.list);
    }



    function updateData(e){
        let value = e.target.value;
        let id    = e.target.id;
        setVisit({...visit,[id]:value});
    }

    function addVisit(){
       // axios.defaults.withCredentials = true;
        axios.post('https://3.35.18.174.nip.io/demo_visit/rest/visit',visit)
        .then((res)=>{
          getData();
          //화면 초기화
          setVisit({name:"",content:"",pwd:""});
        })
        .catch((error)=>{
          console.log(error);
        });  
    }

    function deleteVisit(e){
       
        if(confirm("정말 삭제 하시겠습니까?")==false)return;
       
        //alert(e.target.id + " : delete");
        const idx = e.target.id;
       // axios.defaults.withCredentials = true;
        axios.delete('https://3.35.18.174.nip.io/demo_visit/rest/visit/' + idx)
              .then((res)=>{
                getData();
              })
              .catch((error)=>{
                console.log(error);
              }); 
    }


    useEffect(() => {

       getData();   
       //console.log(data);
    },[]);

    

    return ( 
           <div>
               <div style={{width:"500px",background:"#ccc",margin:"auto",padding:"10px"}}>
                    <h2>글쓰기</h2> 
                    <p>작성자 : <input id="name" value={visit.name} onChange={updateData} /></p>
                    <p><textarea id="content" rows={5} cols={50} onChange={updateData} value={visit.content} ></textarea></p>
                    <p>비밀번호 : <input type="password" id="pwd" value={visit.pwd} onChange={updateData}/></p>
                    
                    <br/>
                    <input type="button" value="글올리기" onClick={ addVisit }/>
                </div>
                <hr/>
                <table className="table table-hover">
                    <thead>
                        <tr className="table-primary">
                            <th width="60%">내용</th>
                            <th width="15%">작성자</th>
                            <th width="15%">작성일자</th>
                            <th width="10%">편집</th>
                        </tr>
                    </thead>
                    <tbody>
                    {data.map(visit => (
                        <tr key={visit.idx}>
                            <td className="content">{visit.content.replaceAll("<br>","")}</td>
                            <td>{visit.name}</td>
                            <td>{visit.regdate}</td>
                            <td><input type="button"  value="삭제" id={visit.idx} onClick={ deleteVisit }/></td>
                        </tr>
                    ))}
                    </tbody>
                </table>

            </div>

    ); 
} 
export default Ex4;